import React, { useContext, useEffect } from 'react'
import { useParams } from 'react-router-dom'
import all_product from '../../assets/all_product'
import starIcon from "../../assets/star_icon.png";
import starEmpty from "../../assets/star_dull_icon.png";
import { ShopContext } from '../../Context/ShopContext';

const ProductDetails = () => {
  const {addCart , cartItems} = useContext(ShopContext)


    let {id} = useParams() ;

    let productMen = all_product.find((ele)=>ele.id ===  parseInt(id))


    
    useEffect(() => {
      console.log(cartItems); 
    }, [cartItems]); 
  
    
  return (
    <>
      <div className="container py-10 flex flex-col lg:flex-row items-start ">

        <div className='flex items-center gap-4 w-full '>
            <div className='flex flex-col gap-2'>
                <img className='w-20' src= {productMen.image} alt= {"productMenImg"} />
                <img className='w-20' src= {productMen.image} alt= {"productMenImg"} />
                <img className='w-20' src= {productMen.image} alt= {"productMenImg"} />
                <img className='w-20' src= {productMen.image} alt= {"productMenImg"} />
            </div>
            <div>
            <img src= {productMen.image} alt= {"productMenImg"} />
            </div>
        </div>


        <div className='space-y-5'>
            <h3 className='text-3xl leading-tight font-semibold'>{productMen.name}</h3>
            <div className='flex items-center gap-2'>
                <img className='w-4' src= {starIcon} alt="starIcon" />
                <img className='w-4' src= {starIcon} alt="starIcon" />
                <img className='w-4' src= {starIcon} alt="starIcon" />
                <img className='w-4' src= {starIcon} alt="starIcon" />
                <img className='w-4' src= {starEmpty} alt="starEmpty" />
            </div>

            <div className='flex items-center gap-6'>
            <p className=' line-through text-gray-500 font-semibold  '> {productMen.old_price} </p>
            <p className='text-orange-600 font-bold'> ${productMen.new_price} </p>
        
            </div>

           <p className='text-sm'>A lightweight , usually knitted , pullover shirt, close fifting and with a round neckline and short sleeves ,worm as an undershirt or outer garment. </p>

           <div className='space-y-3'>
            <p className='font-semibold'>select size</p>
            <div className='flex items-center gap-2'>
                <p className='w-6 h-6 bg-gray-100 cursor-pointer p-5 flex justify-center items-center text-sm'>S</p>
                <p className='w-6 h-6 bg-gray-100 cursor-pointer p-5 flex justify-center items-center text-sm'>M</p>
                <p className='w-6 h-6 bg-gray-100 cursor-pointer p-5 flex justify-center items-center text-sm'>L</p>
                <p className='w-6 h-6 bg-gray-100 cursor-pointer p-5 flex justify-center items-center text-sm'>XL</p>
                <p className='w-6 h-6 bg-gray-100 cursor-pointer p-5 flex justify-center items-center text-sm'>XXL</p>
            </div>

            <button 
  onClick={() => addCart(productMen.id)} 
  className='uppercase bg-orange-600 rounded-lg hover:bg-orange-500 duration-200 transition-all text-gray-50 px-8 py-2 active:scale-90'>
  Add To Cart
</button>

            <p> <span className='font-bold'>Category</span> : { productMen.category} </p>
           </div>
        </div>

      </div>
    </>
  )
}

export default ProductDetails
